import { API_BASE } from '../global/env';
import { ResponseProps } from '../global/util';

export const URL = `${API_BASE}/account`;

export interface AccountProps
{
	id: string,
	dept: string,
	name: string,
	count: number
}

/**
 *   API   
 *
 * @param {string} auth: JWT 
 *
 * @returns {Promise} API  
 */
export async function getAccount(auth: string): Promise<AccountProps | undefined>
{
	let answer: AccountProps | undefined;

	// API  
	try
	{
		const response = await fetch(`${URL}`, {
			method: 'GET',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': auth
			}
		});

		const json = await response.json();

		//   
		if (response.ok)
		{
			answer = json.body;
		}

		//  
		else
		{
			answer = undefined;
		}
	}

	// 
	catch (e)
	{
		answer = undefined;
	}

	return answer;
}

/**
 *  API   
 *
 * @param {string} id: 
 * @param {string} password: 
 *
 * @returns {Promise} API  
 */
export async function postLogin(id: string, password: string): Promise<ResponseProps>
{
	let answer: ResponseProps;

	// API  
	try
	{
		const response = await fetch(`${URL}/login`, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json'
			},
			body: JSON.stringify({
				id: id,
				password: password
			})
		});

		const json = await response.json();

		answer = {
			isSuccess: response.ok,
			body: response.ok ? json.body : json.message
		};
	}

	// 
	catch (e: unknown)
	{
		const err = e as Error;

		answer = {
			isSuccess: false,
			body: err.message
		};
	}

	return answer;
}

/**
 *  
 */
export function postLogout()
{
	localStorage.removeItem('auth');
	location.reload();
}