import { BaseSyntheticEvent, ReactElement, useEffect, useState } from 'react';
import { useSetRecoilState } from 'recoil';
import { postLogin } from '../api/accountApi';
import Meta from '../components/global/Meta';
import { loginAtom } from '../global/state';
import './Login.scss';

/**
 *   ReactElement  
 *
 * @returns {ReactElement} ReactElement 
 */
export default function Login(): ReactElement
{
	const setLoginState = useSetRecoilState(loginAtom);

	const [ errorState, setErrorState ] = useState('');

	useEffect(() =>
	{
		const auth = localStorage.getItem('auth');

		//   
		if (auth !== null)
		{
			setLoginState(auth);
		}
	});

	return (
		<section id='login'>
			<Meta title=' Login' />

			<article id='info'>
				<div>
					<h3>  ?</h3>
					<p>       </p>
					<p>    !</p>
				</div>

				<div>
					<img src='https://accounts.kakao.com/assets/weblogin/techin/retina/banner_login1-81752909ec9c815794b13ccff4d17d7d3e81f431585ecec30ee7a8055785c74f.png' width={540} />
				</div>
			</article>

			<article id='form'>
				<form onSubmit={async (e: BaseSyntheticEvent) =>
				{
					e.preventDefault();

					const id = e.target.elements.id.value;
					const password = e.target.elements.password.value;

					const response = await postLogin(id, password);

					//   
					if (response.isSuccess)
					{
						localStorage.setItem('auth', response.body);
					}

					//  
					else
					{
						setErrorState(response.body);
					}

					setLoginState(response.isSuccess ? response.body : '');
				}}>
					<div className='row'>
						<img src='https://d32gkk464bsqbe.cloudfront.net/company-profiles/o/5818a363e910e621e2d90a2cda4770fef21788a6.png' width={130} />
					</div>

					<div className='row'>
						<input name='id' placeholder=' ' defaultValue='kakaostyle' />
					</div>

					<div className='row'>
						<input type="password" name='password' placeholder='' defaultValue='kakaostyle' />
					</div>

					<div className='row message'>
						{errorState !== '' && (
							<div>
								<p>{errorState}</p>
							</div>
						)}
					</div>

					<div className='row tip'>
						<p><small>TEST ID: kakaostyle</small></p>
						<p><small>TEST PW: kakaostyle</small></p>
					</div>

					<div className='row'>
						<button></button>
					</div>
				</form>
			</article>
		</section>
	);
}