import { API_BASE } from '../global/env';
import { ResponseProps } from '../global/util';

export const URL = `${API_BASE}/vacation`;

export interface VacationProps
{
	idx: number,
	id: string,
	begin: Date,
	end: Date,
	count: number,
	comment?: string,
	reg_date: Date
}

export interface VacationPostProps
{
	begin: Date,
	end: Date,
	comment?: string,
	type: number
}

/**
 *     API   
 *
 * @param {string} auth: JWT 
 *
 * @returns {Promise} API 
 */
export async function getVacations(auth: string): Promise<VacationProps[]>
{
	let answer: VacationProps[];

	// API  
	try
	{
		const response = await fetch(`${URL}`, {
			method: 'GET',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': auth
			}
		});

		const json = await response.json();

		//   
		if (response.ok)
		{
			answer = json.body;
		}

		//  
		else
		{
			answer = [];
		}
	}

	// 
	catch (e)
	{
		answer = [];
	}

	return answer;
}

/**
 *    API   
 *
 * @param {string} auth: JWT 
 * @param {VacationPostProps} vacation:   
 *
 * @returns {Promise} API 
 */
export async function postVacation(auth: string, vacation: VacationPostProps): Promise<ResponseProps>
{
	let answer: ResponseProps;

	// API  
	try
	{
		const response = await fetch(`${URL}`, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': auth
			},
			body: JSON.stringify(vacation)
		});

		//   
		if (response.ok)
		{
			answer = {
				isSuccess: true,
				body: ''
			};
		}

		//  
		else
		{
			const json = await response.json();

			answer = {
				isSuccess: false,
				body: json.message
			};
		}
	}

	// 
	catch (e: unknown)
	{
		const err = e as Error;

		answer = {
			isSuccess: false,
			body: err.message
		};
	}

	return answer;
}

/**
 *    API   
 *
 * @param {string} auth: JWT 
 * @param {number} idx: 
 *
 * @returns {Promise} API 
 */
export async function deleteVacation(auth: string, idx: number): Promise<boolean>
{
	let answer: boolean;

	// API  
	try
	{
		const response = await fetch(`${URL}/${idx}`, {
			method: 'DELETE',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': auth
			}
		});

		answer = response.ok;
	}

	// 
	catch (e)
	{
		answer = false;
	}

	return answer;
}